<template>
  <div id="app">
    <!--start menu sideBar-->
    <article>
      <section class="center-menu bg-black">
        <div class="container mx-auto p-2" style="background-color: #090a09">
          <div class="w-full h-auto bg-black-800 p-2">
            <div class="flex flex-col items-start justify-start space-y-3 bg-black p-1">
              <img :src="require('../assets/images/Spotify_Logo_RGB_White.png')" alt="" class="w-30 h-12 text-sm">
              <button :class="`${setId===items.id ? 'bg-slate-600' : 'bg-black'}`" @click="setId=items.id" v-for="items in menu" :key="items.id" type="button"
                      class=" w-full items-start text-sm text-white font-semibold  transition-all duration-300 hover:bg-gray-500 hover:text-black p-1 rounded-md">
                <div class="flex flex-row items-center">
                  <font-awesome-icon :icon="`${items.icons}`" class="mx-2"></font-awesome-icon>
                  <p>{{ items.name }}</p>
                </div>
              </button>
              <h3 class="text-sm text-slate-200 font-semibold text-start uppercase mt-6 p-1">playlists</h3>
              <div class="flex flex-col items-center mt-2">
                <div class="flex flex-row items-center opacity-50 hover:opacity-100 transition-all duration-300 ml-3" style="cursor: pointer">
                <div class="w-10 h-10 bg-slate-300 mx-2"><font-awesome-icon class="text-black text-center flex justify-center mx-auto p-2" :icon="['fas','plus']"></font-awesome-icon></div>
                  <p class="text-sm text-slate-300 font-bold">Create Playlist</p>
                </div>
                <div class="flex flex-row items-center opacity-50 hover:opacity-100 transition-all duration-300 mt-2" style="cursor: pointer">
                  <div class="w-10 h-10 bg-sky-300 mx-2"><font-awesome-icon class="text-white text-center flex justify-center mx-auto p-2" :icon="['fas','heart']"></font-awesome-icon></div>
                  <p class="text-sm text-slate-300 font-bold">Liked Songs</p>
                </div>
              </div>
              <div class="w-full h-0.5 bg-slate-200 mt-2"></div>
              <div class="w-full h-24 overflow-y-scroll">
                <ul>
                  <li v-for="value in list" :key="value" class="text-slate-200 text-sm font-semibold my-3">{{value.name}}</li>
                </ul>
              </div>
              <button type="button" class="flex flex-row items-center justify-start">
                <font-awesome-icon :icon="['fas','angle-down']" class="border border-slate-300 rounded-full text-white p-1"></font-awesome-icon>
                <p class="text-sm text-slate-400 font-semibold ml-3">Install App</p>
              </button>
              <div class="w-full h-auto relative">
                <div class="w-full h-full absolute opacity-0 hover:opacity-100 p-2 transition-all duration-300"><font-awesome-icon :icon="['fas','angle-up']" class="flex flex-row items-end justify-end border border-slate-400 rounded-full text-white text-md p-1"></font-awesome-icon></div>
                <img class="w-full h-40" :src="require('../assets/images/sahin_robot.jpg')" alt="">
              </div>
            </div>
          </div>
        </div>
      </section>
    </article>
    <!--  end menu sideBar-->
  </div>
</template>

<script>
import {reactive,ref} from "vue";

export default {
  name: "BaseMenu",
  components: {},
  setup() {
    const name=ref('')
    const setId = (() => {
      name.value='home'
    })
    const menu = reactive([
      {id: 'home', name: 'home', icons: 'fas fa-home'},
      {id: 'search', name: 'search', icons: 'fas fa-search'},
      {id: 'library', name: 'library', icons: 'fas fa-book'}
    ])
    const list=reactive([
      {name:"drive"},
      {name:"zhu"},
      {name:"All new India"},
      {name:"Classic road trip Songs"},
      {name:"Lana del Ray"}
    ])
    return {
      setId,
      list,
      name,
      menu,
    }
  }
}
</script>

